const formatTime = (timeStr) => {
    if (!timeStr) return ''
    let time = new Date(timeStr)
    let timeDate = new Date().getTime() - time.getTime()
    if (timeDate < 0) {
      return ''
    } else if ((timeDate / 1000 < 30)) {
      return ''
    } else if (timeDate / 1000 < 60) {
      return ~~(timeDate / 1000) + ''
    } else if ((timeDate / 60000) < 60) {
      return ~~(timeDate / 60000) + ''
    } else if ((timeDate / 3600000) < 24) {
      return ~~(timeDate / 3600000) + ''
    } else if ((timeDate / 86400000) < 31) {
      return ~~(timeDate / 86400000) + ''
    } else if ((timeDate / 2592000000) < 12) {
      return ~~(timeDate / 2592000000) + ''
    } else {
      return ~~(timeDate / 31536000000) + ''
    }
  }

export { formatTime }


